.main {
    padding: 10px;
    height: 100vh;
  }
  .layout {
    display: flex;
  }
  
  .sidebar {
    min-height: 100%;
    min-width: 250px;
    border-radius: 5px;
    background-color: blueviolet;
    box-shadow: 0 0 2px gray;
    margin-right: 20px;
    color: white;
  }
  
  .content {
    width: 100%;
    height: 100%;
    background-color: rgb(145, 63, 220);
  }
  
  .header {
    height: 10vh;
    margin-bottom: 20px;
    /* background-image: url(../images/87ca4db37dc0490c939b68d73abe05dc.png); */
    
  }
  .header-content {
    margin: auto;
    margin-right: 10px;
    background-color: none;
    position: absolute;
    right: 10px;
    top: 32px;
    padding: 10px;
  }
  .bell{
    padding: 3%;
  }
  .bell:hover{
    color: rgb(190, 81, 51);
  }
  .user:hover{
    color: rgb(190, 81, 51);
  }
  .body {
    height: 85vh;
    margin-bottom: 20px;
    /* box-shadow: 0 0 2px gray; */
    background-color: rgb(241, 244, 244);
  }
  .logo h6 {
    font-size: 1.5rem;
    text-align: center;
    margin: 20px 0px;
  }
  .menu {
    margin-top: 50px;
    width: 100%;
  }
  .menu-item {
    margin-top: 30px;
  }
  .menu-item a {
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
  }
  .menu-item i {
    font-size: 1.2rem;
    margin: 0 15px;
  }
  .active {
    background-color: white;
    padding: 10px;
  }
  .active a,
  .active i {
    color: green;
  }

  .active a:hover{
    color: rgb(202, 238, 144);
  }

  a:hover{
    color: rgb(222, 214, 230);
  }

  .header-content{
    display: flex;
    margin: 0 15px;
    align-items: center;
    justify-content: flex-end;
    height: 50px;
  }

  .header-content i{
    margin-right: 20px;
    font-size: 1.2rem;
  }
  .header-content a{
    text-decoration: none;
    color: green;
    margin: 0 10px;
    text-transform: uppercase;
    font-size: 1.2rem;
  }
  .bgimg{
    display: flex;
    /* margin: 50%; */
    background-repeat: no-repeat;
    background-size: 100px;
    display: flex;
    width: 100%;
    height: 12vh;

  }

  .header-text{
    display: flex;
    position: absolute;
    left: 45%;
    top: 4%;
    color: rgb(223, 223, 17);
    /* background-color: black; */
  }
  
  .bgimg img:hover{
    cursor: pointer;
  }
